* {
    font-family: 'Serif'
}

.crudBtn:hover {
    color: rgb(41, 32, 32);
}

.crudBtn:active {
    color: white;
}

.social:hover {
    color: rgb(167, 167, 167);
}

#aboutDiv {
    font-size: calc(1rem + .2vw);
    padding: calc(.5rem + 1vw);
}

#aboutPara {
    font-style: italic;
}

.formInp {
    border: none;
    border-bottom: .5px solid rgb(42, 42, 42);
}

.menuBtn[aria-expanded=false] .fa-caret-up {
    display: none;
}

.menuBtn[aria-expanded=true] .fa-caret-down {
    display: none;
}

@media (max-width:720px) {

    #loginForm,
    #signupForm {
        width: 96%;
    }
    .captchaPara{
        display: block;
    }
}

@media (min-width:720px) {
    #emailRegisteredSignUp {
        position: absolute;
        right: .8rem;
        bottom: .3rem;
    }
}